    //添加文字注记
    map3d.prototype.AddTextLabel  = function (labelName, x, y, text, options)
    {
        options = options ? options : {};    
        var viewer= this.GetView();
        var entity = viewer.entities.getById(labelName);
        var color, pixelOffset;
        if(options){
            color = options.color || Cesium.Color.WHITE ;
            pixelOffset = options.pixelOffset || [0,0]
        }else{
            color = Cesium.Color.WHITE ;
            pixelOffset = [0,0]
        }
        if (!entity)
        {
            var height = options.height ? options.height : 0;
            entity= viewer.entities.add({
                id: labelName,
                position : Cesium.Cartesian3.fromDegrees(parseFloat(x), parseFloat(y), parseFloat(height)),
                label : {
                    text : text,
                    font : '14pt monospace',
                    fillColor : color,
                    style: Cesium.LabelStyle.FILL_AND_OUTLINE,
                    outlineWidth : 2,
                    verticalOrigin : Cesium.VerticalOrigin.Center,
                    pixelOffset : new Cesium.Cartesian2(pixelOffset[0], pixelOffset[1])
                }
            });             
        }
        else
        {
            entity.position.setValue(Cesium.Cartesian3.fromDegrees(parseFloat(x), parseFloat(y)));
            entity.label.text.setValue(text);
        }
        return entity;    
    }

    //
    /* 添加图片注记
       labelName: 注记ID
       x: 经度
       y: 纬度
       aligned: 对齐方式
       convert: 是否转经纬度
       options: { callback: 回调函数 }     
       调用示例：
               map.AddImageLabel("testid", 113, 26, {callback: function(e){} });
    */    
    map3d.prototype.AddImageLabel  = function (labelName, x, y, icondiv, aligned, convert, options)
    {         
         options   = options ? options : {};
         var viewer= this.GetView();
         var entity = viewer.entities.getById(labelName);
         if (!entity)
         {
             entity = viewer.entities.add({
                  id: labelName,
                  position : Cesium.Cartesian3.fromDegrees(parseFloat(x), parseFloat(y)),
                  billboard : {
                        image : icondiv,
                        verticalOrigin: Cesium.VerticalOrigin.BOTTOM,
                        horizontalOrigin: Cesium.HorizontalOrigin.Center
    //                  width : 16,
    //                  height : 16
                  }
                });   
                        
            //
            var scene   = this.GetView().scene;
            var handler = entity.handler = new Cesium.ScreenSpaceEventHandler(scene.canvas);
            handler.setInputAction(function(click)
            {
                var ent = entity;
                var pickedObjects = scene.drillPick(click.position);
                for(var i = 0; i < pickedObjects.length; i++)
                {
                    var pickEnt = pickedObjects[i].id;
                    if (pickEnt.id == ent.id)
                    {
                        if (options.callback)
                        {
                            options.callback(click);
                        }                        
                        break ;
                    }
                }
            }, Cesium.ScreenSpaceEventType.LEFT_CLICK);                                       
         }
         else
         {            
            entity.position.setValue(Cesium.Cartesian3.fromDegrees(parseFloat(x), parseFloat(y)));
         }
          
        return entity;
    }

